<template>
   

    <div class="items" v-if="items.length>0">

        <div class="headItems">
            
            <h2 class="catalog" @click="isVisible=1">Каталог</h2>
            <my-button v-if="isVisible===2||isVisible===3" @click="isVisible--">Назад</my-button>
            <my-input
            v-model="searchQuery"
            placeholder="Поиск"
            class="search">

            </my-input>

            <my-button>Карзина</my-button>
            <select-sort 
            v-bind:selectedSort="selectedSort" 
            v-bind:options="sortOptions"
            @selectedSort="updateSelect">
            
            </select-sort>
            
        </div>

        <transition-group  v-if="isVisible===1" tag="div" class="itemsContainer" name="list">

            <shop-item
         v-for="item in sortedandsearchedItems"
         v-bind:item="item"
         v-bind:url="item.forms[0].variants[0].url"
         :key="item.id"
         @clickCurentItem="updatecurentItems"/>
         
          </transition-group>

          <transition-group v-if="isVisible===2" tag="div" class="itemsContainer" name="list">

            <shop-item
         v-for="item in sortedandsearchedItems"
         v-bind:item="item"
         v-bind:url="item.variants[0].url"
         :key="item.idForm"
         @clickCurentItem="updatecurentForm"/>
         
          </transition-group>
          <transition-group v-if="isVisible===3" tag="div" class="itemsContainer" name="list">

            <shop-item
         v-for="item in sortedandsearchedItems"
         v-bind:item="item"
         v-bind:url="item.url"
         v-bind:price="item.price"
         :key="item.variantsId"
         @clickCurentItem="updatecurentVariants"/>
         
          </transition-group>
        

    </div>

    <h3 class="NoItem" v-else>Список товара пуст...</h3>
    
</template>
<script>
import ShopItem from '@/component/ShopItem'


export default {
    components: {
        ShopItem,
      

    },
    data(){
        return{
            selectedSort:'',
            searchQuery:'',
            sortOptions:[
                {value: 'name', name:'По названию'  },
                {value: 'price', name:'По цене'  } 
            ],
            roadToProduct:{
                id:0  ,
                idForm:0  ,
                variantsId:0  ,   
            },

            curentItems:[],
            curentForm:[],
            isVisible:1,
        
        }
    },
    props:{
        items:{
            type: Array,
            required: true
        }
    },
    
    methods:{
        
        updateSelect(select){
            this.selectedSort=select


        },
        updatecurentItems(curentItems){
            console.log(curentItems)
            this.roadToProduct.id=curentItems.id
            this.curentItems=curentItems;
            this.isVisible++;
        },
        updatecurentForm(curentForm){
            console.log(curentForm)
            this.roadToProduct.idForm=curentForm.idForm
            this.curentForm=curentForm;
            this.isVisible++;

        },
        updatecurentVariants(curentVariant){
            console.log(curentVariant)
            this.roadToProduct.variantsId=curentVariant.variantsId
        }


    },
    
    
    computed:{
        sortedItems(){
            if(this.isVisible===1){
                return[...this.items].sort((item1,item2)=>item1[this.selectedSort]?.localeCompare(item2[this.selectedSort]))
        }else
            if(this.isVisible===2){
                return[...this.curentItems.forms].sort((item1,item2)=>item1[this.selectedSort]?.localeCompare(item2[this.selectedSort]))
        }else
            if(this.isVisible===3){
                if(this.selectedSort==='price'){
                    return[...this.curentForm.variants].sort((item1,item2)=>item1[this.selectedSort]-(item2[this.selectedSort]))
                }else{
                    return[...this.curentForm.variants].sort((item1,item2)=>item1[this.selectedSort]?.localeCompare(item2[this.selectedSort]))
                }
                
        }
        },
        sortedandsearchedItems(){
            //console.log(this.sortedItems)
            return this.sortedItems.filter(item=>item.name.toLowerCase().includes(this.searchQuery.toLowerCase()))
            
            
        }
    },

    
}
</script>
<style scoped>
.items{
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    gap: 30px;
    width: 100%;
    padding: 20px;
    margin: 0 auto;
    width: 1000px;
}
.itemsContainer{
    display: flex;
    flex-wrap: wrap;
    gap: 30px;
}
.headItems{
    display: flex;
    gap: 20px;
    justify-content: space-between;
}
.NoItem{
    margin-top: 5px;
    color: brown;
}
.search{
    width: 100%;
}
.search:focus-visible{
    box-shadow: 0px 0px 10px 0 lightgray,
     0px 0px 3px 0 lightgray;  ;
    outline: none;
}
.catalog{
    cursor: pointer;
}
.catalog:hover{
    text-shadow: 1px 1px 3px lightgray,
    0px 0px 1px lightgray;
}


.list-item {
    display: flex;
  }
  .list-enter-active,
  .list-leave-active {
    transition: all 0.5s ease;
  }
  .list-enter-from,
  .list-leave-to {
    opacity: 0;
    transform: translateX(100px);
  }
  .list-move {
    transition: transform 0.5s ease;
  }
</style>