<template>
    <input class="input" @input="updateInput">
</template>
<script>
export default {
    name:'my-input',
    props:{
        modelValue:[String,Number]
          
    },
    methods:{
       updateInput(event){
        // передаем содержимое инпута
        this.$emit('update:modelValue',event.target.value)

       } 
    }
    
}
</script>
<style scoped>
.input{
    padding: 5px;
    border-radius: 5px;
    border: 1px solid green;
    
}
</style>