<template>
    <select 
    class="selectsSort" 
    @change="changeOption" 
    >
        <option class="selectSort" disabled selected value="">Сортировка</option>

        <option 
        
        v-for="option in options" 
        :key="option.value" 
        :value="option.value"
         >

    {{ option.name }}
    </option>

    </select>
</template>
<script>
export default {
    name:'select-sort',
    
    props:{
        selectedSort:{
            type:String
        },
        options:{
            type:Array,
            default: ()=>[]
        }
    },
    methods: {
        changeOption(event){
            this.$emit('selectedSort',event.target.value)

        }
    },
    
}
</script>
<style scoped>
.selectsSort{
    padding: 5px 10px;
    border-radius: 5px;
    border: 1px solid green;
    background-color: white;
}

    
</style>