
<template>
    <div class="app">

        <header-shop></header-shop>


        <shop-list v-if="isItemsLoading" v-bind:items="items" />
        <div v-else>Идет загрузка товара....</div>


    </div>
</template>

<script>
import HeaderShop from '@/component/HeaderShop';
import ShopList from '@/component/ShopList';

import axios from 'axios'




export default {
    components: {
        ShopList,
        HeaderShop,
        

    },
    data() {
        return {
            items: [],
            isItemsLoading: true,
        }

    },

    methods: {
        async fetchItems() {
            try {
                this.isItemsLoading = !this.isItemsLoading
                const response = await axios.get('https://api.jsonbin.io/v3/b/64073d82ace6f33a22eacace',
                {
                    headers: {'X-Master-Key': '$2b$10$LmiXDcGhxmfG47eeFNmYWOMhtrs9B8lDDOGGXWs4EEVOGieFTB6cS'}
                }
                    )
                this.isItemsLoading = !this.isItemsLoading
                this.items = response.data.record;
            } catch (error) {
                alert('Ошибка')

            } finally {
                //console.log(this.items);
            }
        }
    },
    //обработка запроса постов до построения dom дерева
    mounted() {
        this.fetchItems();
    }

}
</script>

